import { useState } from "react";
import PropTypes from "prop-types";
import React from "react";
import { Link } from "react-router-dom";
import { updateflwruser, updateloginusflw } from "../../services/firebase";

export default function SuggestProfile({
  userDocId,
  username,
  profileId,
  userId,
  maindocId,
}) {
  const [follwed, setFollwed] = useState(false);

  async function handlerfolloing() {
    setFollwed(true);
    await updateloginusflw(maindocId, profileId, false);
    await updateflwruser(userId, userDocId, false);
  }

  return !follwed ? (
    <div className="flex justify-between items-center align-items flex-row">
      <div className="items-center flex justify-between">
        {/* <img className="rounded-full w-8 mr-3"
          src={`images/avatars/${username}.jpg`}
        alt=''
        /> */}

        {username == "Abdelrahman kalefa" ||
        username == "dali" ||
        username == "orwell" ||
        username == "raphael" ? (
          <img
            className="rounded-full w-8 mr-3"
            src={`/images/avatars/${username}.jpg`}
            alt={`${username}`}
          />
        ) : (
          <img
            className="rounded-full w-8 h-8 flex mr-3"
            src={`/images/avatars/default.png`}
            alt={`${username}`}
          />
        )}
        <Link to={`/p/${username}`}>
          <p className="font-bold text-sm">{username}</p>
        </Link>
      </div>
      <button
        type="button"
        onClick={handlerfolloing}
        className="text-sm font-bold text-blue-medium"
      >
        Follow
      </button>
    </div>
  ) : null;
}

SuggestProfile.propTypes = {
  username: PropTypes.string,
  userDocId: PropTypes.string,
  userId: PropTypes.string,
  profileId: PropTypes.string,
};
