import useUser from "../../hooks/use-user";
import React from "react";
import Suggetion from "./suggetion";
import User from "./user";
export default function Sidebare() {
  const {
    user: { docId, username, userId, fullName, following, followers } = {},
  } = useUser();
followers;
  return (
    <div className="p-4 sm:flex  sm:w-1/3 m-auto sm:m-0 w-10/12  flex-col ">
      <User username={username} fullName={fullName} />
      <Suggetion userId={userId} following={following} maindocId={docId} />
    </div>
  );
}

