import PropTypes from "prop-types";
import Header from "./header";
import Image from "./image";
import Actions from "./action";
import Footer from "./footer";
import Comments from "./comment";
import { useRef } from "react";
import React from "react";
export default function Post({ cont }) {
  const commentInput = useRef(null);
  const handleFocus = () => {
    commentInput.current.focus();
  };

  return (
    <div className="bg-white border-gray-primary rounded border mb-12 col-span-4">
      <Header username={cont.username} />
      <Image src={cont.imageSrc} caption={cont.caption} />
      <Actions
        docId={cont.docId}
        totalLikes={cont.likes.length}
        likedPhoto={cont.userLikedphoto}
        handleFocus={handleFocus}
      />
      <Footer username={cont.username} caption={cont.caption} />
      <Comments
        docId={cont.docId}
        comments={cont.comments}
        posted={cont.dateCreated}
        commentInput={commentInput}
      />
    </div>
  );
}

Post.propTypes = {
  cont: PropTypes.shape({
    username: PropTypes.string.isRequired,
    imageSrc: PropTypes.string.isRequired,
    caption: PropTypes.string.isRequired,
    docId: PropTypes.string.isRequired,
    userLikedPhoto: PropTypes.bool,
    likes: PropTypes.array.isRequired,
    comments: PropTypes.array.isRequired,
    dateCreated: PropTypes.number.isRequired,
  }),
};
