import { useState } from 'react';
import PropTypes from 'prop-types';
import { formatDistance } from 'date-fns';
import { Link } from 'react-router-dom';
import React from "react";
import Addcoment from './add-comment';
export default function Comments({ docId, comments: allComments, posted, commentInput }){
const [comments,setComments]=useState(allComments)

    return(<>
    <div className='p-4 pt-1 pb-4'>
        
    {comments.length>=3 && (<p className="text-sm text-gray-base curser-pointer mb-1">view all {comments.length} comment</p>)
        }
        {comments.slice(0, 3).map((item)=>(
<p className='mb-1' key={`${item.comment}-${item.displayName}`}>
<Link to={`/p/${item.displayName}`} >
    <span className='mr-1 font-bold'>{item.displayName}</span>
</Link>
<span>{item.comment}</span>
</p>
        ))}
        <p className='text-gray-base text-xs uppercase mt-2'>{formatDistance(posted,new Date())}</p>
    </div>
    <Addcoment docId={docId} commentInput={commentInput} comments={comments} setComments={setComments}  />
    </>)
}
Comments.propTypes = {
  docId: PropTypes.string.isRequired,
  comments: PropTypes.array.isRequired,
  posted: PropTypes.number.isRequired,
  commentInput: PropTypes.object.isRequired,
};
