import PropTypes from "prop-types";
import { useState, useEffect } from "react";
import React from "react";
import Skeleton from "react-loading-skeleton";
import "react-loading-skeleton/dist/skeleton.css";
import { getsuggetionProfile } from "../../services/firebase";
import SuggestProfile from "./suggestprof";
export default function Suggetion({ userId, following, maindocId }) {
  const [profile, setProfile] = useState(null);

  useEffect(() => {
    async function SuggstedProfile() {
      const response = await getsuggetionProfile(userId, following);
      setProfile(response);
    }

    if (userId) {
      SuggstedProfile();
    }
  }, [userId]);

  return !profile ? (
    <Skeleton count={1} height={150} className="mt-5" />
  ) : (
    <div className="flex-col flex rounded ">
      <div className="flex justify-between items-center align-items mb-5">
        <p className="font-bold text-gray-base">Suggetion For you </p>
      </div>
      <div className="mt-4 grid gap-5">
        {profile.map((prof) => (
          <SuggestProfile
            key={prof.docId}
            userDocId={prof.docId}
            username={prof.username}
            profileId={prof.userId}
            userId={userId}
            maindocId={maindocId}
          />
        ))}
      </div>
    </div>
  );
}

Suggetion.propTypes = {
  userId: PropTypes.string,
  following: PropTypes.array,
  maindocId: PropTypes.string,
};
