import Post from "./Post";
import React from "react";
import Skeleton from "react-loading-skeleton";
import "react-loading-skeleton/dist/skeleton.css";
import usePhoto from "../hooks/use-photos";
export default function TimeLine() {
  const { photo } = usePhoto();

  return (
    <div className="c container sm:flex m-auto sm:m-0  sm:w-2/3  w-full  flex-col">
      {!photo ? (
        <>
          {[...new Array(4)].map((_, index) => (
            <Skeleton key={index} width={320} height={400} count={1} />
          ))}
        </>
      ) : photo?.length > 0 ? (
        photo.map((cont) => <Post cont={cont} key={cont.docId} />)
      ) : (
        <p>hey fowllo pepole</p>
      )}
    </div>
  );
}
