import { useEffect, useState } from "react";


import { getphoto, getUseobjbyId } from "../services/firebase";
import useAuthListener from "../hooks/use-auth-listener";
export default function usePhoto() {
  const [photo, setPohto] = useState(null);
  const {
    user: { uid: userId = "" },
  } = useAuthListener();
  useEffect(() => {
    async function getTimeLinePhotoes() {
      const [{ following }] = await getUseobjbyId(userId);
      let followeduserPhoto = [];

      if (following.length > 0) {
        followeduserPhoto = await getphoto(userId, following);
      }
      followeduserPhoto.sort((a, b) => a.dateCreated - b.dateCreated);
      setPohto(followeduserPhoto);
    }

    getTimeLinePhotoes();
  }, [userId]);
  return { photo };
}
